.image {
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 200px;
  border-radius: 2px;
  margin: 1rem;
  position: relative;
  width: 250px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  max-width:100%;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s; /* Animation */
}
  
.img-responsive{
  max-width:100%;
  -o-object-fit: none;
     object-fit: none;
}

.image:hover{
  cursor: pointer;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1); 
}

 


